* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list,
.country-info{
  list-style: none;
  margin-left: 20;
}
.country-list__item {
  display: flex;
}
.country-list__item:not(:last-child) {
  margin-bottom: 30px;
}
.country-list__item-name{
  font-size: 24px;
  margin-left: 20px;
}
.country-info__item-name{
  font-size: 36px;
}
.country-info__item-text{
  font-size: 24px;
  font-weight: 500;
}